
































































import { validationMixin } from 'vuelidate'
import { required, helpers } from 'vuelidate/lib/validators'
import Vue from 'vue'
import Form from '@/components/Form.vue'

export default Vue.extend({
  name: 'App',
  mixins: [validationMixin],
  components: { Form },
  data() {
    return {
      fields: {
        fname: '',
        sname: '',
        birth: '',
        number: '',
        group: '',
        city: '',
        doc: '',
        docDate: ''
      },
      completed: false
    }
  },
  methods: {
    chechFields() { 
      this.$v.fields.$touch()
      this.completed = true
      if (!this.$v.fields.$error) this.completed = true
    }
  },
  validations: {
    fields: {
      fname: {
        required
      },
      sname: {
        required
      },
      birth: {
        required
      },
      number: {
        validate(value) {
          return value ? /^7\d{10}$/gm.test(value) : false
        }
      },
      group: {
        required
      },
      city: {
        required
      },
      doc: {
        required
      },
      docDate: {
        required
      }
    }
  }
})
