



























import Vue from 'vue'

export default Vue.extend({
  props: {
    label: {
      required: true
    },
    placeholder: {},
    length: {
      type: Number
    },
    options: {
      type: Array
    },
    error: {
      type: Boolean
    }
  },
  data() { return { value: '' } },
  methods: {
    rt() {
      this.$emit('value', this.value)
    }
  }
})
